.container {
	width: 375px;
	margin: 0 auto;
	padding-left: 20px;
	padding-right: 20px;
}

@media screen and (min-width: 768px) {
	.container {
		width: 768px;
		padding-left: 32px;
		padding-right: 32px;
	}
}

@media screen and (min-width: 1440px) {
	.container {
		width: 1440px;
		padding-left: 100px;
		padding-right: 100px;
	}
}